@import '../values/colors.css';

.lug-link,
.lug-link:visited {
    text-decoration: none;
    color: var(--nord10);
}

.lug-link:hover,
.lug-link:active,
.lug-link:visited:hover,
.lug-link:visited:active {
    color: var(--nord8)
}